import { PhotosType, ProfileType } from '../types/types';
import { instance, APIResponseType } from './api.ts';

export const profileAPI = {
  getProfile(userId: number) {
    return instance.get<ProfileType>(`profile/${userId}`).then((res) => res.data);
  },
  saveProfileInfo(profileData: ProfileType) {
    return instance.put<APIResponseType>(`profile`, profileData).then((res) => res.data);
  },
  getStatus(userId: number) {
    return instance.get<string>(`profile/status/${userId}`).then((res) => res.data);
  },
  updateStatus(status: string) {
    return instance
      .put<APIResponseType<{ status: string }>>(`profile/status`, { status })
      .then((res) => res.data);
  },
  updatePhoto(photoFile: File) {
    const formData = new FormData();
    formData.append('image', photoFile);
    return instance
      .put<APIResponseType<PhotosType>>(`/profile/photo`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
      .then((res) => res.data);
  },
};
